(function () {
    'use strict';
    //‰∏∫‰∫ÜÂú®ÈùûÊ≤πÁå¥ÁéØÂ¢É‰∏ãÂ≠òÂÇ®‰æùÊóßËÉΩËµ∑‰∏ÄÈÉ®ÂàÜÁöÑ‰ΩúÁî®
    if (window.hasOwnProperty("GM_getValue") && window.hasOwnProperty("GM_setValue")) {
        localStorage.getItem = (<any>window).GM_getValue;
        localStorage.setItem = (<any>window).GM_setValue;
    }
    //ÂØπÊú¨Âú∞ÊâìÂºÄÁöÑÁΩëÈ°µÁöÑ‰øÆÊîπ ÈúÄË¶ÅÂú®ÊµèËßàÂô®‰∏≠ËÆæÁΩÆÂÖÅËÆ∏Âú®Êñá‰ª∂Âú∞ÂùÄ‰∏äËøêË°å

    /** Â≠òÂÇ®Èº†Ê†áÊâÄÂú®‰ΩçÁΩÆÁöÑÊâÄÊúâÂÖÉÁ¥† */
    let path:HTMLElement[];
    /** ÁõëÂê¨Èº†Ê†áÁßªÂä® */
    function mouse(event:Event) {
        if (event.target instanceof HTMLElement) {
            path = nodePath(event.target);
            outline(event.target);
        }
    }
    const global = {
        state: 0,
        elemtEdit: false,
    };
    //ÁõëÊµãÊåâÈîÆ‰∫ã‰ª∂
    document.addEventListener('keydown', function (event) {
        var code = event.code;
        if (code === 'F2') {
            global.elemtEdit = !global.elemtEdit;
            console.log('ÂàáÊç¢ÁºñËæëÁä∂ÊÄÅ', global.elemtEdit );
            if (global.elemtEdit) //‰∏çÂ§Ñ‰∫éÁºñËæëÁä∂ÊÄÅÂàôÁßªÈô§Èº†Ê†áÁõëÂê¨‰∫ã‰ª∂ÔºåÈôç‰ΩéÊÄßËÉΩÁöÑÊ∂àËÄó
                document.addEventListener('mouseover', mouse);
            else
                document.removeEventListener("mouseover", mouse);
            event.preventDefault();
            event.returnValue = false;
            return false;
        }
        //ÊúâÂÖÉÁ¥†Ëé∑ÂæóÁÑ¶ÁÇπÔºåËßÜ‰∏∫Ê≠£Âú®ËæìÂÖ•ÊñáÊú¨Ôºå‰∏çÊâßË°å‰∏ãÈù¢ÁöÑÂäüËÉΩ
        if (document.querySelectorAll(":focus").length > 0) {
            return true;
        }
        switch (code) {
            case 'KeyQ':
                editSelect();
                break;
            case 'KeyD':
                deleteSelect();
                break;
            case 'KeyC':
                copyTitle(); //Â§çÂà∂title  ËøôÈáå‰∏çÂä†breakÊòØ‰∏∫‰∫Ü‰∏çÂΩ±ÂìçÊ≠£Â∏∏ÁöÑÂ§çÂà∂Ë°å‰∏∫
            case "KeyW":
                console.log("path", path);
                break;
            default:
                return true;
        }
    });
    /** ÁõëÂê¨ÁÑ¶ÁÇπ‰∫ã‰ª∂ÔºåÁî®‰∫éÂà§Êñ≠ÂÖÉÁ¥†ÊòØÂê¶Ë¢´‰øÆÊîπ */
    function focus(event:Event) {
        console.log(event);
    }
    document.addEventListener('focus', focus, true); //useCapture  ÂèÇÊï∞ËÆæ‰∏∫trueÊù•ÂÆûÁé∞‰∫ã‰ª∂ÂßîÊâòÔºå‰ΩÜ‰∏çÂêåÊµèËßàÂô®ÁöÑÂÆûÁé∞ÂèØËÉΩ‰∏çÂêå.....
    /** ËÆæÁΩÆÂÖÉÁ¥†ÂèØÁºñËæëÂπ∂Ëé∑Âèñ ÈÄêÁ∫ßÂêë‰∏äËé∑Âèñtitile*/
    function editSelect() {
        var selectElem = path[0];
        selectElem.contentEditable = 'true';
        copyTitle();
    }
    var div = document.createElement('div');
    div.style.display = "none";
    /** ÁßªÈô§ÈÄâ‰∏≠ÁöÑÂÖÉÁ¥† ‰∏ç‰ΩøÁî®remove ÊòØÂõ†‰∏∫Ëøô‰∏™ÊñπÊ≥ïÂπ∂Ê≤°ÊúâÁúüÊ≠£Âà†Èô§ */
    function deleteSelect() {
        div.appendChild(path[0]);
        div.innerHTML = "";
    }
    var input = document.createElement('input');
    input.setAttribute('type', 'hidden');
    input.setAttribute('readonly', 'readonly');
    document.body.appendChild(input);
    /**
    * ËÆæÁΩÆ‰∏Ä‰∏™ÂΩ±ËóèÁöÑÊñáÊú¨Ê°ÜÁî®Êù•Â§çÂà∂ÊñáÊú¨
    */
    function copyTitle() {
        //Ëé∑ÂèñÂÖÉÁ¥†ÁöÑÊèèËø∞Âπ∂Â∞Ü‰ªñ‰ª¨Ê∑ªÂä†Âà∞Ââ™Ë¥¥Êùø  ÁõÆÂâçÊîØÊåÅmdn ÂÖ∂ÂÆÉÁöÑÂèØËÉΩÊîØÊåÅ
        var title;
        //ËøôÈáåÊäõÂºÉÂêé‰∏§‰∏™ÂÖÉÁ¥†ÊòØÂõ†‰∏∫‰ªñ‰ª¨‰∏çÊòØ‰∏ÄËà¨ÁöÑelemÂÖÉÁ¥†‰∫Ü
        for (var index = 0; index < path.length - 2; index++) {
            title = path[index].getAttribute("title");
            if (title)
                break;
        }
        input.setAttribute('value', title);
        input.select();
        document.execCommand('copy'); //Â§çÂà∂
    }
    function outline(elemt:HTMLElement) {
        if (elemt.style.outline == "2px solid red")
            return;
        elemt.style.outline = "2px solid red";
        setTimeout(function () {
            if (elemt == path[0]) {
                outline(elemt);
                return;
            }
            elemt.style.outline = "";
        }, 500);
    }
    /**
     * Ëé∑Âèñ‰∏Ä‰∏™ÂÖÉÁ¥†ÁöÑÊâÄÊúâÁà∂ËäÇÁÇπÂà∞html‰∏∫Ê≠¢
     */
    function nodePath(...path:HTMLElement[]) {
        while (path[path.length-1].parentElement != null) {
            path.push(path[path.length - 1].parentElement);
        }
        return path;
    }
})();
